
import axios from 'axios';
import MProgress from '../components/progress'
import { toast } from 'react-toastify';
import { url } from '../config/ips';

var allurl = ''
const instance = axios.create({
    baseURL: '/',//'https://xudaxianer.cn',////
    timeout: 10000,
    // headers: { 'X-Custom-Header': 'foobar' }
});

//
instance.interceptors.request.use(function (config) {
    // 
    MProgress.start();
    allurl = config.url
   
    return config;
}, function (error) {
    // 
    return Promise.reject(error);
});

//
instance.interceptors.response.use(function (response) {
    // 
    MProgress.done();
    return response;
}, function (error) {
    // 
    MProgress.done();
    if (!error.response) {
        if (error.message.indexOf("timeout") > -1) {
            toast.warning("")
        } else {
            toast.warning("")
        }
    } else {
        switch (error.response.status) {
            case 400:
                toast.warning("")
                break;
            case 403:
                toast.warning("")
                break;
            case 404:
                toast.warning("")
                break;
            case 408:
                toast.warning("")
                break;
            case 500:
                toast.warning("")
                break;
            case 501:
                toast.warning("")
                break;
            case 502:
                toast.warning("")
                break;
            case 503:
                toast.warning("")
                break;
            case 504:
                toast.warning("")
                break;
            default:
                toast.warning(`${error.response.status}`)
                break;
        }
    }

    
    return Promise.reject(error);
});


export const NetPost = async (api, params) => {
            return new Promise((resolve, reject) => {
                
                instance.post(api, params)
                    .then(res => {
                        resolve(res.data)
                    })
                    .catch(error => {
                        console.log("error", error)
                        reject(error)
                    })
            })
}


export const NetGet = async (api, params) => {

    return new Promise((resolve, reject) => {
        
        instance.get(api, {params:params})
            .then(res => {
                resolve(res.data)
            })
            .catch(error => {
                console.log("error", error)
                reject(error)
            })
    })


}




