import "./index.css"


class Belt extends Component {

    render() {
        return (
            <div>
                <div id="ban-content" className="ban-conter" >
                    <div className="world_box">
                        <div className="world"><h1></h1></div>
                        <div>
                        <p id="poem"></p>
                        <p id="info"></p>
                        </div>
                    </div>
                </div>
                
            </div>
        )
    }
}



export default connect()(Belt)